import React, {useState} from 'react';
import { PayPalScriptProvider, PayPalButtons } from '@paypal/react-paypal-js';
import { Modal } from "@mui/material";
import { CLIENT_ID } from "../utils/Const";
import { NotificationManager } from 'react-notifications';
//import { useState } from 'react';

const SubscriptionOption = ({planId, handleSubscription, closeModal}) => {
  const [success, setSuccess] = useState(false);
  //const [subscriptionId, setSubscriptionId, setCreatedEmail, setCreatedAmount, setCreatedTime] = useState(null);
  
  const handleSubscriptionSuccess = (details) => {
    const {
      subscriptionID,
      payerID,
      payerName,
      payerEmail,
      amount,
      createTime,
      updateTime,
      links,
    } = details;
    //handleUserCreation();
    
    setSuccess(true);

    closeModal();

    if (handleSubscription) {
      handleSubscription(details);
    }

    //closeModal();
  }

  return (

      <PayPalButtons
        style={{
          shape: 'pill',
          color: 'gold',
          layout: 'vertical',
          label: 'subscribe'
        }}
        createSubscription={(data, actions) => {
          return actions.subscription.create({
            'plan_id': planId, 
            'userAction': "SUBSCRIBE_NOW"
          })
        }}
        onApprove={handleSubscriptionSuccess}
        onError={(error) => {
          NotificationManager.error('Subscription failed: ' + error.message);
        }}
      />
  );
};

export default SubscriptionOption;