import React, { useEffect, useState } from 'react';
import SubscriptionOption from './SubscriptionOption';
import { CLIENT_ID, ANNUAL_PLAN, MONTHLY_PLAN } from '../utils/Const';

function SubscriptionList({ handleSubscription, closeModal }) {
  const [paypalLoaded, setPaypalLoaded] = useState(false);

  useEffect(() => {
    // Check if the PayPal script is already loaded
    if (window.paypal) {
      setPaypalLoaded(true);
    } else {
      // Load the PayPal SDK script dynamically when the component is mounted
      const script = document.createElement('script');
      script.src = `https://www.paypal.com/sdk/js?client-id=${CLIENT_ID}`;
      script.async = true;

      script.onload = () => {
        // PayPal SDK is loaded, set the flag to true
        setPaypalLoaded(true);
      };

      document.body.appendChild(script);
    }

    return () => {
      // No need to remove the script, as it will be reused if the component is remounted
    };
  }, []);

  const handleSubscriptionSuccess = (details) => {
    // Subscription is successful
    handleSubscription(details);
  };

  return (
    <div>
      <h2 style={{ textAlign: 'center' }}>Choose a Subscription:</h2>
      <p></p>
      {paypalLoaded ? (
        <div className="subscription-option" style={{ textAlign: 'center' }}>
          <h3>Monthly Subscription</h3>
          <p>$225 USD per month</p>
          <SubscriptionOption planId={MONTHLY_PLAN} handleSubscription={handleSubscription} closeModal={closeModal} />
        </div>
      ) : null}

      {paypalLoaded ? (
        <div className="subscription-option" style={{ textAlign: 'center' }}>
          <h3>Annual Subscription</h3>
          <p>$2600 USD per year</p>
          <SubscriptionOption planId={ANNUAL_PLAN} handleSubscription={handleSubscription} closeModal={closeModal}/>
        </div>
      ) : null}
    </div>
  );
}

export default SubscriptionList;
